import Nav from './Nav';
import {Link} from "react-router-dom";
import {React} from "react";

import ShoppingBagOutlinedIcon from '@mui/icons-material/ShoppingBagOutlined';

function Header() {

    const cartProducts = getCartProducts();

    function getCartProducts() {

        let cartProductsTmp = localStorage.getItem('cartProducts');

        if (!cartProductsTmp) return;

        cartProductsTmp = JSON.parse(cartProductsTmp);

        if (!cartProductsTmp) return 0;

        return cartProductsTmp.reduce((quantity, product) => {
            quantity += product.quantity;
            return quantity;
        }, 0);
    }

    return (
        <header className="header">
            <div className="container">
                <Nav/>

                <div className="header__cart">
                    <span id = "cartCount" className="header__cart_count">{(cartProducts)? cartProducts.length:0}</span>
                    <Link to="/Cart/"><ShoppingBagOutlinedIcon fontSize="large"/></Link>
                </div>

            </div>
        </header>
    );
}

export default Header;