import {React, useContext, useEffect, useRef, useState} from "react";
import {MainContext} from "../Main";
import {Link} from "react-router-dom";

import DeleteForeverOutlinedIcon from '@mui/icons-material/DeleteForeverOutlined';

function Cart() {

    const scrollNode = document.documentElement;
    scrollNode.scrollTop = 0

    const {productsCatalog, cartProducts, setCartProducts, bagSize} = useContext(MainContext);

    useEffect(() => {
        const quantityInputElems = document.getElementsByClassName("item__quantity_input");

        for (let i = 0; i < quantityInputElems.length; i++) {
            let currentProductId = quantityInputElems[i].parentNode.dataset.id;

            let cartProductsTmp = cartProducts.filter((item, index) => currentProductId == item.id);

            quantityInputElems[i].value = (cartProductsTmp) ? cartProductsTmp[0].quantity : 1;
        }

    }, [cartProducts]);

    function changeProductQuantity(e, quantity, currentProductId) {
        const currentInputElem = e.currentTarget.parentNode.firstChild;

        if (!currentInputElem) return
        const quantityTmp = ((currentInputElem.value) ? +currentInputElem.value : 1) + quantity;

        const cartProductsTmp = cartProducts.map((item, index) => {
                if (currentProductId == item.id && quantityTmp > 0) item.quantity = quantityTmp;
                return item;
            }
        )

        setCartProducts([...cartProductsTmp]);
    }

    function getTotalPrice() {
       const totalPrice = cartProducts.reduce((sum, product) => {
            sum += product.prices[product.bagSize] * product.quantity;

            return sum;
        }, 0)

        return Math.round(totalPrice * 100) / 100;
    }

    function remove(e, currentProductId) {
        const cartProductsTmp = cartProducts.filter((product, index) => currentProductId != product.id);

        if (cartProductsTmp.length === 0) clearStorage()

        setCartProducts([...cartProductsTmp]);
    }

    function clearStorage() {
        localStorage.removeItem('cartProducts');
    }

    function get() {

        return cartProducts.map((item, index) => {

            let productImg = "/" + item.img + "_280x.jpg";

            return (
                <li key={index} data-id={item.id} className="productsCart__item">
                    <Link to={`/products/${item.id}/`} className="productsCart__item_wrapper">
                        <div className="productsCart__item_img"><img src={productImg} alt="#"/></div>
                        <div className="productsCart__item_description">
                            <div className="productsCart__item_description-name">{item.name}</div>
                            <div
                                className="productsCart__item_description-bagSize">{bagSize[item.bagSize] + ' lb'}</div>
                        </div>
                    </Link>

                    <div data-id={item.id} className="productsCart__item_quantity">
                        <input onChange={(e) => changeProductQuantity(e, 0, item.id)} className="item__quantity_input"
                               type="text" min="1"
                               defaultValue={item.quantity}/>

                        <button onClick={(e) => changeProductQuantity(e, -1, item.id)}
                                className="item__quantity_btn-left">-
                        </button>
                        <button onClick={(e) => changeProductQuantity(e, 1, item.id)}
                                className="item__quantity_btn-right">+
                        </button>
                    </div>

                    <div className="productsCart__item_price">
                        {"$" + Math.round(item.prices[item.bagSize] * item.quantity * 100) / 100}
                    </div>

                    <button onClick={(e) => remove(e, item.id)} className="item__quantity_btn-delete">
                        <DeleteForeverOutlinedIcon fontSize="large"/>
                    </button>
                </li>
            );
        });
    }

    return (
        <div className="page">
            <div className="container">
                <ul className="productsCart__items">
                    {get()}
                </ul>

                <div className="productsCart__totalPrice">
                    {"Total: $" + getTotalPrice()}
                </div>
            </div>
        </div>
    );
}

export default Cart;