import { Link } from "react-router-dom";
import Logo from "../images/Registered_CBD_Logos_Red_Web_Simple_Horizontal_200x.png";

function Nav() {
	return (
		<nav className="header__nav">
            <ul>
                <li><Link to="/"><img src={Logo} alt="Coffee Bean Direct header logo" /></Link></li>
                <li>
                    <Link to="/Shop/">Shop</Link>
                    <ul className="header__nav_sub">
                        <li><Link to="/RoastedCoffee/">Roasted coffee</Link></li>
                        <li><Link to="/UnroastedCoffee/">Unroasted coffee</Link></li>
                    </ul>
                </li>
                <li>
                    <Link to="/Subscriptions/">Subscriptions</Link>
                    <ul className="header__nav_sub">
                        <li><Link to="/Subscriptions/">Subscriptions</Link></li>
                        <li><Link to="/GiftSubscriptions/">Gift subscriptions</Link></li>
                    </ul>
                </li>
            </ul>
        </nav>
	);
}

export default Nav;

