import {useContext, useRef, useEffect, useState, React} from "react";
import {MainContext} from "../Main";

import Accordion from "../Accordion";
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import NavigateBeforeIcon from '@mui/icons-material/NavigateBefore';
import {Link} from "react-router-dom";

function Shop() {

    const scrollNode = document.documentElement;
    scrollNode.scrollTop = 0

    const {productsCatalog} = useContext(MainContext);

    const [filterProductsCatalog, setFilterProductsCatalog] = useState(productsCatalog);

    const [filters, setFilter] = useState({
        roast: [],
        region: [],
        body: [],
        acidity: [],
        tastingNotes: []
    });

    const [catalogPagesCount, setCatalogPagesCount] = useState(Math.ceil(productsCatalog.length / 20));

    const [currentCatalogPage, setCurrentCatalogPage] = useState(1);

    const subMenu = {
        roast: ['Light', 'Light-Medium', 'Medium', 'Medium-Dark', 'Dark'],
        region: [],
        body: ['Smooth', 'Smooth-Balanced', 'Balanced', 'Balanced-Heavy', 'Heavy'],
        acidity: ['Low', 'Low-Medium', 'Medium', 'Medium-High', 'High'],
        tastingNotes: []
    };

    useEffect(() => {
        if (filterProductsCatalog.length) setCatalogPagesCount(Math.ceil(filterProductsCatalog.length / 20));
    }, [filterProductsCatalog]);

    useEffect(() => {

    }, [currentCatalogPage]);

    function get() {
        return filterProductsCatalog.map((item, index) => {

            let productImg = "/" + item.img + "_280x.jpg";

            if(index >= (currentCatalogPage - 1) * 20 && index <currentCatalogPage * 20) {

                return (
                    <Link to={`/products/${item.id}/`} key={index} className="product__item">
                        <img src={productImg} alt="#"/>
                        <div className="product__item_description">
                            <div className="product__item_description-name">{item.name}</div>
                            <div
                                className="product__item_description-price">{Math.min(...item.prices) + ' - ' + Math.max(...item.prices) + ' /lb'}</div>
                        </div>
                    </Link>
                );
            }
        });
    }

    function getSubMenu() {

        productsCatalog.forEach(item => {

            if (item.region && !subMenu.region.includes(item.region)) {
                item.region.forEach(region => {
                    if (!subMenu.region.includes(region)) subMenu.region.push(region);
                })
            }

            if (item.tastingNotes && !subMenu.tastingNotes.includes(item.tastingNotes)) {
                item.tastingNotes.forEach(tastingNote => {
                    if (!subMenu.tastingNotes.includes(tastingNote)) subMenu.tastingNotes.push(tastingNote);
                })
            }

        });

        subMenu.region.sort();
        subMenu.tastingNotes.sort();
    }

    function checkFilter(event) {
        const subMenuTitle = event.currentTarget.parentNode.id;
        const filter = subMenu[subMenuTitle][event.currentTarget.id];

        if (!subMenuTitle || !filter) return;


        if (filters[subMenuTitle].includes(filter)) {
            filters[subMenuTitle] = filters[subMenuTitle].filter(item => item !== filter)
        } else {
            filters[subMenuTitle].push(filter);
        }


        let productsCatalogTmp = productsCatalog.filter(item => {

            let regionTmp = item.region.filter(region => filters.region.includes(region));
            let tastingNotesTmp = item.tastingNotes.filter(tastingNote => filters.tastingNotes.includes(tastingNote));

            return (
                (filters.roast.length === 0 || filters.roast.includes(item.roast))
                && (filters.region.length === 0 || regionTmp.length > 0)
                && (filters.body.length === 0 || filters.body.includes(item.body))
                && (filters.acidity.length === 0 || filters.acidity.includes(item.acidity))
                && (filters.tastingNotes.length === 0 || tastingNotesTmp.length > 0)
            )
        })

        setFilterProductsCatalog([...productsCatalogTmp]);
        setCurrentCatalogPage(1);
    }

    function getAccordionSubMenu(nameSubMenu) {
        if (subMenu[nameSubMenu].length === 0) return;

        return subMenu[nameSubMenu].map((item, index) => {

            return (
                <li onClick={checkFilter} key={index} id={index}>
                    <label className="tag__checkbox-wrapper text-label">
                        <input type="checkbox" className="tag__input" name="filter.p.m.my_fields.roast_percentage_"
                               value={item}/>
                        <span onClick={(e) => e.stopPropagation()}>{item}</span>
                    </label>
                </li>
            );

        });
    }

    function getCatalogPages() {
        if (!filterProductsCatalog.length) return;
        const catalogPagesArr = [];

        for (let i = 1; i <= Math.max(catalogPagesCount, 0); i++) {
            catalogPagesArr.push(
                <li onClick={showCatalogPage} className={(currentCatalogPage == i)?"active":""} key={i} data-id={i}>{i}</li>
            )
        }

        return catalogPagesArr;
    }

    function showCatalogPage(event, next) {
        let pageNum = 1;

        if (next === 1) {
            pageNum = currentCatalogPage + 1;
        } else if (next === -1) {
            pageNum = currentCatalogPage - 1;
        } else {
            pageNum = event.currentTarget.dataset.id;
            event.currentTarget.parentNode.childNodes.forEach(node => node.classList.remove("active"));
            event.currentTarget.classList.add("active");
        }

        setCurrentCatalogPage(pageNum);
    }


    return (

        <div className="page">

            {getSubMenu()}

            <div className="container">

                <div className="productsCatalog__wrapper">

                    <div className="accordion">
                        <Accordion
                            title="Roast Level"
                            subMenuKey='roast'
                            list={getAccordionSubMenu('roast')}
                        />
                        <Accordion
                            title="Region"
                            subMenuKey='region'
                            list={getAccordionSubMenu('region')}
                        />
                        <Accordion
                            title="Body"
                            subMenuKey='body'
                            list={getAccordionSubMenu('body')}
                        />
                        <Accordion
                            title="Acidity"
                            subMenuKey='acidity'
                            list={getAccordionSubMenu('acidity')}
                        />
                        <Accordion
                            title="Tasting Notes"
                            subMenuKey='tastingNotes'
                            list={getAccordionSubMenu('tastingNotes')}
                        />
                    </div>

                    <div className="productsCatalog">
                        <div className="productsCatalog__items">
                            {get()}
                        </div>

                        {catalogPagesCount >= 2 &&
                        <div className="productsCatalog__btns">
                            {currentCatalogPage > 1 &&
                            <button onClick={(e) => showCatalogPage(e,-1)} className="productsCatalog__btn prev">
                                <NavigateBeforeIcon/>
                            </button>
                            }

                            <ul className="productsCatalog__btns_pages">
                                {getCatalogPages()}
                            </ul>

                            {catalogPagesCount != currentCatalogPage &&
                            <button onClick={(e) => showCatalogPage(e,1)} className="productsCatalog__btn next">
                                <NavigateNextIcon/>
                            </button>
                            }
                        </div>
                        }
                    </div>

                </div>

            </div>
        </div>

    );
}

export default Shop;