import {Link} from "react-router-dom";
import {React} from "react";

function Footer() {
    return (
        <footer className="footer">
            <div className="footer__top">
                <div className="container">
                </div>
            </div>

            <div className="footer__bot">
                <div className="container">

                    <div className="footer__bot_wrapper">
                        <div className="footer__bot_list">
                            <div className="footer__bot_title">Menu</div>
                            <ul>
                                <li><Link to="#">About Us</Link></li>
                                <li><Link to="#">Commercial</Link></li>
                                <li><Link to="#">Subscribe to Save</Link></li>
                                <li><Link to="#">FAQ</Link></li>
                                <li><Link to="#">Contact Us</Link></li>
                            </ul>
                        </div>

                        <div className="footer__bot_list contacts">
                            <div className="footer__bot_title">Get in touch</div>
                            <ul>
                                <li><Link to="#"><img src="/phone.png" alt="#"/>888-2326711</Link></li>
                                <li><Link to="#"><img src="/email.png" alt="#"/>Email us</Link></li>
                            </ul>
                        </div>

                        <div className="footer__bot_list">
                            <div className="footer__bot_title">Store Hours</div>
                            <ul>
                                <li><span>Mon-Thu: </span>8:30am-4:30pm ET</li>
                                <li><span>Fri: </span>8:30am-4:00pm ET</li>
                            </ul>
                        </div>

                        <div className="footer__bot_list-wrapper">
                            <div className="footer__bot_list payCard">
                                <div className="footer__bot_title">We accept</div>
                                <ul>
                                    <li><img src="/visa-credit-card.png" alt="#"/></li>
                                    <li><img src="/mastercard.png" alt="#"/></li>
                                    <li><img src="/google-pay.png" alt="#"/></li>
                                    <li><img src="/apple-pay.png" alt="#"/></li>
                                    <li><img src="/amazon-pay-icon-small-black.png" alt="#"/></li>
                                </ul>
                            </div>

                            <div className="footer__bot_list social">
                                <div className="footer__bot_title">Follow us</div>
                                <ul>
                                    <li><Link to="#"><img src="/instagram.png" alt="#"/></Link></li>
                                    <li><Link to="#"><img src="/facebook.png" alt="#"/></Link></li>
                                    <li><Link to="#"><img src="/twitter.png" alt="#"/></Link></li>
                                </ul>
                            </div>
                        </div>
                    </div>

                </div>

                <div className="footer__bot_copyrights">
                    <div className="container">
                        &copy; 2022 Coffee Bean Direct All Rights Reserved.
                    </div>
                </div>
            </div>

        </footer>
    );
}

export default Footer;